import React from 'react'
import './Detail.css'

const Detail = ({ inv }) => {
    console.log(inv);
    const fields = []
    for (let [key, value] of Object.entries(inv)) {
        fields.push(value)
    }
    return (
        <div className="detail">
            <img src={inv.image} alt={inv.uri} width="100%" height="auto" />
            <h4>{inv.title}</h4>
            <ul>
                {fields.map((field, key) =>
                    <li key={key}>
                        {field}
                    </li>
                )}
            </ul>
            <p>
                {inv.tags.map((tag, i) => <small key={i}>{tag}</small>)}
                <br></br>
                {inv.description}
            </p>
        </div>
    )
}

export default Detail
